import React from 'react';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';

// import { usePapaParse } from 'react-papaparse';
import Papa from 'papaparse';

class CSVDisplay extends React.Component {

  constructor(props)
  {
    super(props);
    this.state = {
      csvHead: [],
      csvData: []
    }
  }

  componentDidMount()
  {
    this.parseFile(String(this.props.csvContent));
    // this.parseFile('Study,Acronym,Disease Area/Condition,Intervention and Comparator,Primary Outcome/Endpoint,Blinding,Size,Findings,Conclusion\nEffects of Once-Weekly Exenatide on Cardiovascular Outcomes in Type 2 Diabetes, EXSCEL,Cardiology,Once-Weekly Exenatide vs Placebo,"first occurrence of death from cardiovascular causes, nonfatal myocardial infarction, or nonfatal stroke",Double-blind,"14,752","A primary composite outcome event occurred in 839 of 7356 patients (11.4%; 3.7 events per 100 person-years) in the exenatide group and in 905 of 7396 patients (12.2%; 4.0 events per 100 person-years) in the placebo group (hazard ratio, 0.91; 95% confidence interval [CI], 0.83 to 1.00), with the intention-to-treat analysis indicating that exenatide, administered once weekly, was noninferior to placebo with respect to safety (P<0.001 for noninferiority) but was not superior to placebo with respect to efficacy (P=0.06 for superiority).","Among patients with type 2 diabetes with or without previous cardiovascular disease, the incidence of major adverse cardiovascular events did not differ significantly between patients who received exenatide and those who received placebo."\n"Effects of Once-Weekly Exenatide on Cardiovascular Outcomes in Type 2 Diabetes", EXSCEL,Cardiology,Once-Weekly Exenatide vs Placebo,"first occurrence of death from cardiovascular causes, nonfatal myocardial infarction, or nonfatal stroke",Double-blind,"14,752","A primary composite outcome event occurred in 839 of 7356 patients (11.4%; 3.7 events per 100 person-years) in the exenatide group and in 905 of 7396 patients (12.2%; 4.0 events per 100 person-years) in the placebo group (hazard ratio, 0.91; 95% confidence interval [CI], 0.83 to 1.00), with the intention-to-treat analysis indicating that exenatide, administered once weekly, was noninferior to placebo with respect to safety (P<0.001 for noninferiority) but was not superior to placebo with respect to efficacy (P=0.06 for superiority).","Among patients with type 2 diabetes with or without previous cardiovascular disease, the incidence of major adverse cardiovascular events did not differ significantly between patients who received exenatide and those who received placebo."');
  }

  parseFile(file) {
    Papa.parse(file, {
      // header: true,
      complete: (results)=> {  
          this.setState(
            {
              csvHead: (results.data).slice(0, 1)[0],
              csvData: (results.data).slice(1)
            }
          )
      }// do something,
    });
  };

  render()
  {
    return (
      // <table>{ this.arrsToTable(this.state.csvData) }</table>
      <Table key = {Math.random()}><Thead><Tr>{(this.state.csvHead).map((el, ind)=>(<Th key={el+ind}>{el}</Th>))}</Tr></Thead><Tbody>{(this.state.csvData).map((arr, ind)=>(<Tr key={arr+ind}>{Array.from(arr).map((el, indx)=><Td key={el+indx}>{el}</Td>)}</Tr>))}</Tbody></Table>
      // <p>{typeof(this.state.csvData)}</p>
    );
  }
}

export default CSVDisplay