import React from 'react';

// import Button from '@mui/material/Button';
// import ButtonGroup from '@mui/material/ButtonGroup';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

// const [age, setAge] = React.useState('');

class YearInput extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state = {
            year: "2023",
            openState: false,
          }
    }

    handleChange = (event) => {
        let a=String(event.target.value);
        this.setState({year: a});
        this.props.updateFunc(a);
        this.handleClose();
    }
    
    setOpen = (openS) => {
        this.setState({
            openState: openS
        });
    };
            

    handleClose = () => {
        this.setOpen(false);
    };

    handleOpen = () => {
        this.setOpen(true);
    };

    render()
    {
        return(
            // <ButtonGroup variant="contained" aria-label="outlined primary button group">
            // <Button>One</Button>
            // <Button>Two</Button>
            // <Button>Three</Button>
            // </ButtonGroup>
            <Box sx={{ minWidth: 120 }}>
            <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Year Published</InputLabel>
                <Select
                    open={this.state.openState}
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={this.state.year}
                    label="Year Publisehd"
                    onChange={this.handleChange}
                    onClose={this.handleClose}
                    onOpen={this.handleOpen}
                >
                    {/* [println("<MenuItem value={"*string(i)*"}>"*string(i)*"</MenuItem>") for i in 2022:-1:1990]; */}
                    <MenuItem value={2023}>2023</MenuItem>
                    <MenuItem value={2022}>2022</MenuItem>
                    <MenuItem value={2021}>2021</MenuItem>
                    <MenuItem value={2020}>2020</MenuItem>
                    <MenuItem value={2019}>2019</MenuItem>
                    <MenuItem value={2018}>2018</MenuItem>
                    <MenuItem value={2017}>2017</MenuItem>
                    <MenuItem value={2016}>2016</MenuItem>
                    <MenuItem value={2015}>2015</MenuItem>
                    <MenuItem value={2014}>2014</MenuItem>
                    <MenuItem value={2013}>2013</MenuItem>
                    <MenuItem value={2012}>2012</MenuItem>
                    <MenuItem value={2011}>2011</MenuItem>
                    <MenuItem value={2010}>2010</MenuItem>
                    <MenuItem value={2009}>2009</MenuItem>
                    <MenuItem value={2008}>2008</MenuItem>
                    <MenuItem value={2007}>2007</MenuItem>
                    <MenuItem value={2006}>2006</MenuItem>
                    <MenuItem value={2005}>2005</MenuItem>
                    <MenuItem value={2004}>2004</MenuItem>
                    <MenuItem value={2003}>2003</MenuItem>
                    <MenuItem value={2002}>2002</MenuItem>
                    <MenuItem value={2001}>2001</MenuItem>
                    <MenuItem value={2000}>2000</MenuItem>
                    <MenuItem value={1999}>1999</MenuItem>
                    <MenuItem value={1998}>1998</MenuItem>
                    <MenuItem value={1997}>1997</MenuItem>
                    <MenuItem value={1996}>1996</MenuItem>
                    <MenuItem value={1995}>1995</MenuItem>
                    <MenuItem value={1994}>1994</MenuItem>
                    <MenuItem value={1993}>1993</MenuItem>
                    <MenuItem value={1992}>1992</MenuItem>
                    <MenuItem value={1991}>1991</MenuItem>
                    <MenuItem value={1990}>1990</MenuItem>
                </Select>
            </FormControl>
            </Box>
        );
    }

}

export default YearInput;