import React from 'react';

import DoctorTypeInput from '../components/DoctorTypeInput'
import YearInput from '../components/YearInput'

class UserInput extends React.Component
{
    constructor(props)
    {
        super(props)
        this.state=({
            profession: "General Medicine",
            year: 2023
        });
    }

    parentUpdateProf = (newProf) => {
        this.setState({profession: newProf});
    }

    parentUpdateYear = (newYear) => {
        this.setState({year: newYear});
    }

    submitForm()
    {
        this.props.updateFunc(this.state.profession, this.state.year)
    }

    render()
    {
        return(
            <div>
                <DoctorTypeInput updateFunc={this.parentUpdateProf}/>
                <YearInput updateFunc={this.parentUpdateYear}/>
                <button onClick={()=>this.submitForm()}>Default</button>;
            </div>
        );
    }
}

export default UserInput;