import React from 'react';

// import Button from '@mui/material/Button';
// import ButtonGroup from '@mui/material/ButtonGroup';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

// const [age, setAge] = React.useState('');

class DoctorTypeInput extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state = {
            profession: "General Medicine",
            openState: true,
          }
    }

    handleChange = (event) => {
        let a=String(event.target.value);
        this.props.updateFunc(a);
        this.setState({profession: a});
        this.handleClose();
    }
    
    setOpen = (openS) => {
        this.setState({
            openState: openS
        });
    };
            

    handleClose = () => {
        this.setOpen(false);
    };

    handleOpen = () => {
        this.setOpen(true);
    };

    render()
    {
        return(
            // <ButtonGroup variant="contained" aria-label="outlined primary button group">
            // <Button>One</Button>
            // <Button>Two</Button>
            // <Button>Three</Button>
            // </ButtonGroup>
            <Box sx={{ minWidth: 120 }}>
            <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Profession</InputLabel>
                <Select
                    open={this.state.openState}
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={this.state.profession}
                    label="Profession"
                    onChange={this.handleChange}
                    onClose={this.handleClose}
                    onOpen={this.handleOpen}
                >
                    <MenuItem value={"General Medicine"}>General Medicine</MenuItem>
                    <MenuItem value={"Oncology"}>Oncology</MenuItem>
                    <MenuItem value={"Cardiology"}>Cardiology</MenuItem>
                    <MenuItem value={"Endocrinology"}>Endocrinology</MenuItem>
                </Select>
            </FormControl>
            </Box>
        );
    }

}

export default DoctorTypeInput;