import React from 'react';
// import { BrowserRouter as Router, Routes, Route} from 'react-router-dom';

import UserInput from './pages/UserInput';
import DisplayOutput from './pages/DisplayOutput';
import SiteComponents from './pages/SiteComponents';

class App extends React.Component {

  constructor(props)
  {
    super(props)
    this.state = (
      {
        profession: "General Medicine",
        year: 2023,
        csvResponse: "loading",
        onInput: true
      }
    );
  }

  // userInputUpdateFunc = (profession, year) => {
  //   this.setState({
  //     profession: profession,
  //     year: year
  //   });
  //   fetch("/", {
  //     method: 'POST',
  //     headers: {
  //       'Content-Type': 'application/json'
  //     },
  //     body: JSON.stringify({
  //       profession: profession,
  //       year: year
  //     })
  //   })
  //   .then(console.log(profession))
  //   .then(response=>response.text())
  //   .then(response=>this.setState({csvResponse: response}))
  //   .then(()=>this.setState({onInput: false}));
  // }

  userInputUpdateFunc = (profession, year) => {
    this.setState({
      profession: profession,
      year: year
    });
  
    const fileName = `${year}_${profession}.csv`; // Use template literals to construct the file name

    fetch(`/database/${fileName}`) // Update the URL to the path of your text file
      .then(response => response.text())
      .then(text => {
        this.setState({ csvResponse: text });
        this.setState({ onInput: false });
      })
      .catch(error => {
        console.error("Error fetching text file:", error);
        // Handle the error condition here, if needed
      });
  }
  

  render() {
    if (this.state.onInput) {
      return (
        <div>
          <SiteComponents />
          <UserInput updateFunc={this.userInputUpdateFunc}/>
        </div>
      );
    }
    else {
      return (
        <div>
          <SiteComponents />
          <DisplayOutput updateCSVContent={this.state.csvResponse} />
        </div>
        );
    }
    // return (
    //   <Router>
    //     <SiteComponents />
    //     <Routes>
    //       <Route exact path='/' element={<UserInput updateFunc={this.userInputUpdateFunc}/>} />
    //       <Route path='/output' element={<DisplayOutput />} />
    //       </Routes>
    //   </Router>
    // );
  }
}

export default App;