import React from 'react';

import CSVDisplay from '../components/CSVDisplay';
import CSVDownloadButton from '../components/CSVDownloadButton';

class DisplayOutput extends React.Component
{
    render()
    {
        return(
            <div>
                {/* <CSVDisplay csvContent='Study,Acronym,Disease Area/Condition,Intervention and Comparator,Primary Outcome/Endpoint,Blinding,Size,Findings,Conclusion */}
                {/* Effects of Once-Weekly Exenatide on Cardiovascular Outcomes in Type 2 Diabetes, EXSCEL,Cardiology,Once-Weekly Exenatide vs Placebo,"first occurrence of death from cardiovascular causes, nonfatal myocardial infarction, or nonfatal stroke",Double-blind,"14,752","A primary composite outcome event occurred in 839 of 7356 patients (11.4%; 3.7 events per 100 person-years) in the exenatide group and in 905 of 7396 patients (12.2%; 4.0 events per 100 person-years) in the placebo group (hazard ratio, 0.91; 95% confidence interval [CI], 0.83 to 1.00), with the intention-to-treat analysis indicating that exenatide, administered once weekly, was noninferior to placebo with respect to safety (P<0.001 for noninferiority) but was not superior to placebo with respect to efficacy (P=0.06 for superiority).","Among patients with type 2 diabetes with or without previous cardiovascular disease, the incidence of major adverse cardiovascular events did not differ significantly between patients who received exenatide and those who received placebo." */}
                {/* "Effects of Once-Weekly Exenatide on Cardiovascular Outcomes in Type 2 Diabetes", EXSCEL,Cardiology,Once-Weekly Exenatide vs Placebo,"first occurrence of death from cardiovascular causes, nonfatal myocardial infarction, or nonfatal stroke",Double-blind,"14,752","A primary composite outcome event occurred in 839 of 7356 patients (11.4%; 3.7 events per 100 person-years) in the exenatide group and in 905 of 7396 patients (12.2%; 4.0 events per 100 person-years) in the placebo group (hazard ratio, 0.91; 95% confidence interval [CI], 0.83 to 1.00), with the intention-to-treat analysis indicating that exenatide, administered once weekly, was noninferior to placebo with respect to safety (P<0.001 for noninferiority) but was not superior to placebo with respect to efficacy (P=0.06 for superiority).","Among patients with type 2 diabetes with or without previous cardiovascular disease, the incidence of major adverse cardiovascular events did not differ significantly between patients who received exenatide and those who received placebo."'/> */}
                {/* <CSVDisplay /> */}
                <CSVDisplay csvContent={this.props.updateCSVContent} />
                <CSVDownloadButton FileName="paperscrape.csv" Text={this.props.updateCSVContent} />
            </div>
        );
    }
}

export default DisplayOutput;